
.CatalogPageHeader {
  display: block;
  background: var(--clr-blue);
  padding: 3rem 15% 4rem;
  color: var(--clr-white);
}
.CatalogPageHeader-logo img {
  height:50px;
}
.CatalogPageHeader-header a {
  float: left;
  color: black;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
}

.CatalogPageHeader-header-content {
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.02em;
  text-transform: capitalize;
}

.CatalogPageHeader-header-content h2 {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 42px;
  line-height: 60px;
  margin: 3rem 0 1rem;
}

.CatalogPageHeader-header a.CatalogPageHeader-logo {
  font-size: 25px;
  font-weight: bold;
}

.CatalogPageHeader-header a:hover {
  background-color: #ddd;
  color: black;
}

.CatalogPageHeader-header a.active {
  background-color: dodgerblue;
  color: white;
}

.CatalogPageHeader-header-right {
  float: right;
}

@media screen and (max-width: 500px) {
  .CatalogPageHeader-header a {
    float: none;
    display: block;
    text-align: left;
  }

  .CatalogPageHeader-header-right {
    float: none;
  }
}
